import React from 'react';
import Link from 'next/link'
import Head from 'next/head'

const NavLink = (props) => (
  <Link prefetch href={props.href}>
    <a className="nav-link">
      {props.children}
    </a>
  </Link>
)

const domainURL = "https://tianhui.li"
const imageURL = domainURL + "/static/images/tianhuil.jpg"

const Header = () => (
  <div>
    <nav className="navbar navbar-expand-sm mb-5">
      <div className="container">
        <div className="navbar-brand">
          <NavLink href="/" >Michael Li</NavLink>
        </div>
        <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
          <span className="navbar-toggler-icon"></span>
        </button>

        <div className="collapse navbar-collapse" id="navbarSupportedContent">
          <ul className="navbar-nav ml-auto">
            <li className="nav-item">
              <NavLink className="navbar-link" href="/">Home</NavLink>
            </li>
            <li className="nav-item">
              <NavLink href="/professional">Professional</NavLink>
            </li>
            <li className="nav-item">
              <NavLink href="/writing"> Writing </NavLink>
            </li>
          </ul>
        </div>
      </div>
    </nav>
    <style jsx>{`
      .navbar {
        border-bottom: 1px solid #dddddd;
        background-color: transparent;
        padding: 6px 16px;
      }

      .navbar-brand {
        color: rgb(25,160,210);
        font-weight: 200;
        font-size: 46px;
        padding: 0px;
      }
      .navbar-brand a {
        padding: 0px;
      }

      .navbar-nav li a {
        font-size: 18px;
        color: #aaaaaa;
        font-weight: 100;
      }

      .navbar-nav li.active a {
        color: #dddddd;
      }

      .navbar-nav li a:hover,
      .navbar-nav li a:focus {
        background-color: transparent;
        color: #dddddd;
      }
    `}</style>
  </div>
)

const HeadDiv = () => (
  <Head>
    <meta name="viewport" content="width=device-width, initial-scale=1" />
    <meta charSet="utf-8" />
    <meta charSet="utf-8" />
    <meta httpEquiv="X-UA-Compatible" content="IE=edge" />
    <meta name="viewport" content="width=device-width, initial-scale=1" />
    <title>Michael Li</title>

    {/* Facebook */}
    <meta property="og:title"       content="Michael Li"/>
    <meta property="profile:first_name" content="Michael" />
    <meta property="profile:last_name"  content="Li" />
    <meta property="profile:gender"     content="male" />
    <meta property="og:type"        content="profile" />
    <meta property="og:description" content="Personal Homepage for Tianhui Michael Li"/>
    <meta property="og:image"       content={imageURL}/>
    <meta property="og:url"         content={domainURL}/>

    {/* Twitter */}
    <meta name="twitter:card"         content="summary" />
    <meta name="twitter:site"         content="@tianhuil" />

    {/* Stylesheet */}
    <link rel="stylesheet"
      href="https://stackpath.bootstrapcdn.com/bootstrap/4.1.3/css/bootstrap.min.css"
      integrity="sha384-MCw98/SFnGE8fJT3GXwEOngsV7Zt27NXFoaoApmYm81iuXoPkFOJwJ8ERdknLPMO"
      crossOrigin="anonymous"/>

    {/* Favicons from https://www.favicon-generator.org/ */}
    <link rel="apple-touch-icon" sizes="57x57" href="/static/favicons/apple-icon-57x57.png" />
    <link rel="apple-touch-icon" sizes="60x60" href="/static/favicons/apple-icon-60x60.png" />
    <link rel="apple-touch-icon" sizes="72x72" href="/static/favicons/apple-icon-72x72.png" />
    <link rel="apple-touch-icon" sizes="76x76" href="/static/favicons/apple-icon-76x76.png" />
    <link rel="apple-touch-icon" sizes="114x114" href="/static/favicons/apple-icon-114x114.png" />
    <link rel="apple-touch-icon" sizes="120x120" href="/static/favicons/apple-icon-120x120.png" />
    <link rel="apple-touch-icon" sizes="144x144" href="/static/favicons/apple-icon-144x144.png" />
    <link rel="apple-touch-icon" sizes="152x152" href="/static/favicons/apple-icon-152x152.png" />
    <link rel="apple-touch-icon" sizes="180x180" href="/static/favicons/apple-icon-180x180.png" />
    <link rel="icon" type="image/png" sizes="192x192"  href="/static/favicons/android-icon-192x192.png" />
    <link rel="icon" type="image/png" sizes="32x32" href="/static/favicons/favicon-32x32.png" />
    <link rel="icon" type="image/png" sizes="96x96" href="/static/favicons/favicon-96x96.png" />
    <link rel="icon" type="image/png" sizes="16x16" href="/static/favicons/favicon-16x16.png" />
    <link rel="manifest" href="/static/favicons/manifest.json" />
    <meta name="msapplication-TileColor" content="#ffffff" />
    <meta name="msapplication-TileImage" content="/static/favicons/ms-icon-144x144.png" />
    <meta name="theme-color" content="#ffffff" />
  </Head>
)

class Layout extends React.Component {
  render() {
    return (
      <div>
        <HeadDiv />
        <Header />
        <section>
          <div className="container">
            {this.props.children}
          </div>
        </section>
        <div></div>  {/* footer */}
        <style jsx global>{`
          h1 {
            color: rgb(25,160,210);
            font-weight: 200;
            font-size: 36px;
          }

          h2 {
            font-weight: 100;
            color: #888888;
            font-size: 20px;
          }

          body a {
            color: rgb(25,160,210);
          }

          body p {
            font-weight: 200;
            color: #888888;
            font-size: 16px;
          }
        `}</style>
      </div>
    )
  }
}

export default Layout



